<script>
	import { onMount } from "svelte";
	import { scaleLinear } from "d3-scale";
	import { dataArrayStore, dataBinsStore, hoverDatumStore, selectLabelStore, selectPredictedStore } from "../store";
	import { fade, fly } from 'svelte/transition';
	import ScoreBoxHist from './ScoreBoxHist.svelte';
    import { fileprefix, classmap } from "../globals.js"

	let selectedDatum;

	hoverDatumStore.subscribe(datum => {
		if (datum) {
			selectedDatum = datum;
		}
		else
			console.warn('Selected dataum not valid (SelectedImg)', {datum});
	});

	const binWidth = 70;
	const binHeight = 50;
	const lm = 60;  // left margin for labels
	const tm = 10;  // top margin for labels
	const chartwidth = binWidth*10;
	const chartheight = binHeight*10;
	const classColorBck = 15;
	console.log(chartwidth+lm)

	function scaleH(score) {  // scale histogram x
		return lm + chartwidth*score
	}

	function selectPrediction(i) {
		// when a user clicks a "Predicted as k" text, images whose predicted_label = k will be highlighted. If "Labeled as i" has been highlighted, the and operation will be applied to the filtering condition (i.e. true_label = i and predicted_label = k)
		console.log(i)
		if ($selectPredictedStore != -1) {
			selectPredictedStore.set(-1); // toggle off
			return
		}
		// else
		selectLabelStore.set(-1); // overwrite label store
		hoverDatumStore.set(false); // overwrite selected datum store
		selectPredictedStore.set(i); 
	}

	function selectLabel(i) {
		// when a user clicks the "Labeled as i" text again, everything will be back to the previous state. If a user clicks another "Labeled as j" text, then, only the images that meet the new criteria will be highlighted (i.e. true_label = j)
		console.log(i)
		if ($selectLabelStore != -1) {
			selectLabelStore.set(-1); // toggle off
			return
		}
		// else, set
		selectPredictedStore.set(-1);  // overwrite predicted store
		hoverDatumStore.set(false); // overwrite selected datum store
		selectLabelStore.set(i); 
	}

</script>

{#if $dataBinsStore && $dataBinsStore[0]}
	<svg viewBox="0 0 {chartwidth+lm} {chartheight+tm}" >
		{#if $hoverDatumStore}
			<g class="hist">
				{#each $hoverDatumStore.predicted_scores as score, i}
					<g in:fly="{{ x: -100, duration: 500 }}" out:fade>
						<line x1={lm} x2={scaleH(score)} y1={tm+i*binHeight} y2={tm+i*binHeight+5} />  <!--  top edge -->
						<line x1={scaleH(score)} x2={scaleH(score)} y1={tm+i*binHeight+5} y2={tm+(1+i)*binHeight-5} />  <!--  hist right end -->
						<line x1={lm} x2={scaleH(score)} y1={tm+(1+i)*binHeight} y2={tm+(1+i)*binHeight-5} />  <!--  bottom edge -->
					</g>
				{/each}
			</g>
		{/if}
		<g class="chartArea" transform="translate({lm} {tm})">
			{#each $dataBinsStore as classData, i_class}
				<g class="classRow" height={binHeight}>
					{#each classData.bins as binData, i_bin}
						<ScoreBoxHist 
							{binData} {binHeight} {binWidth}
							tx={i_bin * binWidth}
							ty={i_class * binHeight}
						/>
					{/each}
				</g>
			{/each}
		</g>
		<g class="axis">
			<g class="marks">
				{#each Array(10) as _, i}
					<line x1={lm-2} x2={chartwidth+lm} y1={tm+i*binHeight} y2={tm+i*binHeight} />  <!-- Vertical axis -->
					<g transform="translate(0 {tm+(i+0.5)*binHeight})">
						<rect class="datum-color"
							transform="translate(38 -13)"
							width={12} height={15} 
							fill={classmap[i]} stroke={classmap[i]}
							/>
						<text class="class-label">
							Class {i}
						</text>
						<text class="axis-text clickable-text" 
							transform="translate(0 11)" 
							on:click={()=>selectLabel(i)} 
							text-decoration={$selectLabelStore == i ? "underline" : "none"}
							>
								Labeled {i}
						</text>
						<text class="axis-text clickable-text" transform="translate(0 22)"
							on:click={()=>selectPrediction(i)} 
							text-decoration={$selectPredictedStore == i ? "underline" : "none"}
							>
								Predicted {i}
						</text>
					</g>
					<line x1={lm+i*binWidth} x2={lm+i*binWidth} y1={tm+2} y2={tm-2} />  <!--  Horizontal axis -->
					<text x={lm+(i-0.1)*binWidth} y="{tm-2}" class="axis-text">{(i*0.1).toPrecision(1)}</text>
				{/each}
			</g>
			<line x1={lm} y1={tm} x2={lm+chartwidth} y2={tm} />
			<line x1={lm} y1={tm} x2={lm} y2={tm+chartheight}/>
		</g>
	</svg>
{:else}
	<p>Data not loaded</p>
{/if}

<style>
	.clickable-text {
		cursor: pointer;
	}

	svg {
		margin: 5px;
	}

	.hist {
		stroke-width: 2;
		stroke: black;
	}

	.classRow {
		stroke-width: 1;
		stroke: lightgray;
	}

	.class-label {
		font-size:medium; 
		stroke: none;
	}

    .axis-text { 
		font-size: x-small; 
		stroke: none;
	}


	.axis {
		stroke: lightgray;
	}
</style>
