<script>
	import { ScoreDist, SelectedImg, ProjView } from './components/index'
	import { onMount } from "svelte";
	import { dataArrayStore, dataBinsStore, hoverDatumStore } from "./store";
	//import { scaleLinear } from "d3-scale";

	const numClasses = 10;
	const numBins = 10;
	let binsByClasses = [];
    let instances;

	onMount(async () => {
		const fetched = await fetch("./static/prediction_results.json");
		instances = (await fetched.json()).test_instances;

		// Transform data
		for (let k = 0; k < numClasses; ++k) { // k is classes
			let binsForClass = [];
			for (let b = 0; b < numBins; ++b) {  // b is bin number
				const binInstances = instances
					.filter(datum => datum.true_label == k) // is current class (k)
					.filter(datum => {  // is in current bin (b)
						let binMin = b * 0.1;
						let binMax = binMin + 0.1;
						let score = datum.predicted_scores[k];
						return score >= binMin && score <= binMax;
					}
				); 
				binsForClass.push({"class": k, "binNo": b, "instances": binInstances});
			}
			binsByClasses.push({"class": k, "bins": binsForClass});
		}

		console.log({binsByClasses, instances});
		dataArrayStore.set(instances);
		dataBinsStore.set(binsByClasses);
	});

</script>

