				<ProjView />
			</div>
			<SelectedImg />
		</div>
		<div id="main-section" style="width: 1000px;">
			<div id="score-distributions-view" class="view-panel">
				<div class="view-title">Score Distributions</div>
				<ScoreDist />
			</div>
		</div>
	</div>
</main>

