<script>
	import { onMount } from "svelte";
	import { scaleLinear } from "d3-scale";
	import { dataArrayStore, dataBinsStore, hoverDatumStore } from "../store";
    import { fileprefix, classmap } from "../globals.js"

	export let binData;
    export let tx;
    export let ty;
    export let binWidth;
    export let binHeight;

    const imgSize = 8;
    const mod = (a,n) => ((a % n ) + n ) % n; // modulo operator https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Remainder

    function selectDatum(datum, i) {
        console.log('selected datum in scoreboxhist', {datum, i});
        hoverDatumStore.set({...datum});
    }

    function clearDatum() {
        hoverDatumStore.set(false);
    }

    function calcX(i) {
        const maxPer = Math.floor(binWidth / imgSize);
        const xi = i % maxPer;
        const x = imgSize * xi;
        return x
    };


    function calcY(i) {
        const maxPer = Math.floor(binWidth / imgSize);
        const yi = Math.floor(i / maxPer);
        const y = yi * imgSize;
        return y
    };

</script>



<g transform="translate({tx} {ty})">
{#each binData.instances as datum, i}
    <g transform="translate({calcX(i)} {binHeight - imgSize - calcY(i)})">
        <rect class="datum-color" 
            width={imgSize-2} height={imgSize-2} 
            fill={classmap[datum.true_label]} stroke={classmap[datum.predicted_label]}
            opacity={$hoverDatumStore && $hoverDatumStore.id != datum.id ? '0.3' : '1.0'}
        />
        <image class="datum-img" 
            href={fileprefix + datum.filename} 
            alt="datum {datum.id}" 
            on:mouseenter={()=>selectDatum(datum, i)}
            on:mouseleave={clearDatum}
            width={imgSize-2}
        />
    </g>
{/each}
</g>



<style>
	.datum-img {
	}

    .datum-color {
        stroke-width: 2px;
    }
</style>
