<script>
	import { } from "svelte";
	import { fade } from 'svelte/transition';

	import { scaleLinear } from "d3-scale";
	import { dataArrayStore, hoverDatumStore, selectLabelStore, selectPredictedStore } from "../store";
    import { fileprefix, classmap } from "../globals.js"

    const margin = {t: 10, b: 2, l: 20, r: 5 }
    const height = 100;
    const width = 100;
	let yScale = false;
    let xScale = false;

    dataArrayStore.subscribe(data => {
        if (!data) { return }

        const xproj = [
            Math.min(...data.map(datum=>datum.projection[0])),
            Math.max(...data.map(datum=>datum.projection[0])),
        ]
        const yproj = [
            Math.min(...data.map(datum=>datum.projection[1])),
            Math.max(...data.map(datum=>datum.projection[1])),
        ]
        yScale = scaleLinear()
            .domain(xproj)
            .range([margin.t, height-margin.t-margin.b]);
        xScale = scaleLinear()
            .domain(yproj)
            .range([margin.l, width-margin.l-margin.r]);

        console.log(
            yproj, xproj,
            [margin.l, width-margin.l-margin.r],
            xScale(data[0].projection[0]),
            yScale(data[0].projection[1])
        );
    });

    function selectDatum(datum, i) {
        console.log('selected datum in scoreboxhist', {datum, i});
        hoverDatumStore.set({...datum});
    }

    function clearDatum() {
        hoverDatumStore.set(false);
    }

	// responsive stroke logic: $hoverDatumStore && $hoverDatumStore.id != datum.id ? classmap[datum.predicted_label] : undefined
</script>

{#if $dataArrayStore && $dataArrayStore[0] && xScale && yScale}
    <svg viewBox="0 0 {width} {height}" >
        {#each $dataArrayStore as datum, i}
			<!-- <circle 

				r={$hoverDatumStore ? ($hoverDatumStore.id == datum.id ? "2" : "0.5") : "5"} 
				opacity={$hoverDatumStore ? 
					($hoverDatumStore.id != datum.id ? '0.3' : '1.0') : '0.8'}

				cx={xScale(datum.projection[0])} 
				cy={yScale(datum.projection[1])}
				fill={'none' || classmap[datum.true_label]} 
				stoke={classmap[datum.predicted_label]}
				stroke-width="0.5"  
				
				/> -->
			{#if $hoverDatumStore && $hoverDatumStore.id == datum.id} <!-- a datum is selected/highlighted, make big -->
				<circle transition:fade
					on:mouseenter={()=>selectDatum(datum, i)}
					on:mouseleave={clearDatum}
					cx={xScale(datum.projection[0])} 
					cy={yScale(datum.projection[1])}
					r={$hoverDatumStore.id == datum.id ? "2" : "0.5"} 
					opacity='0.9'
					fill={classmap[datum.true_label]}
					stroke={classmap[datum.predicted_label]}
					stroke-width={$hoverDatumStore.id != datum.id ? '0.25' : '1.0'}
					/>
			{:else if $hoverDatumStore 
				|| ($selectPredictedStore != -1 && $selectPredictedStore != datum.predicted_label)
				|| ($selectLabelStore != -1 && $selectLabelStore != datum.true_label) }
				<!-- a predicted score is selected/highlighted and this is not one of those, make opaque -->
				<circle transition:fade
					on:mouseenter={()=>selectDatum(datum, i)}
					on:mouseleave={clearDatum}
					cx={xScale(datum.projection[0])} 
					cy={yScale(datum.projection[1])}
					r="0.5"
					opacity='0.2'
					fill={classmap[datum.true_label]}
					stroke={classmap[datum.predicted_label]}
					stroke-width="0.25" 
					/>
			{:else} <!-- normal render -->
				<circle transition:fade
					on:mouseenter={()=>selectDatum(datum, i)}
					on:mouseleave={clearDatum}
					cx={xScale(datum.projection[0])} 
					cy={yScale(datum.projection[1])}
					r="0.5" 
					opacity='0.8'
					fill={classmap[datum.true_label]}
					stroke={classmap[datum.predicted_label]}
					stroke-width="0.25" 
					/>
			{/if}
        {/each}

	</svg>

{:else}
	<p>Data not loaded</p>
{/if}

<style>
	svg {
		margin: 5px;
	}
</style>
